<template>
  <div id="installation-items">
      <div>
        <table>
            <thead>
                <tr>
                    <th class="vanish-cell"></th>
                    <th>No.</th>
                    <th>Installation Item</th>
                    <th>Qty</th>
                    <th>Unit</th>
                    <th>Price</th>
                    <th>Amount</th>
                    <th>Note</th>
                    <th class="vanish-cell"></th>
                </tr>
            </thead>
            <tbody>
            <tr v-for="(item,index) in initialItems" v-dragging="{ item: item, list: initialItems, group: 'item' }" :key="index" @click="rowClicked(index,$event)">

                <template v-if="item.desc != 'Consumption Tax'">
                    <td class="move-btn" title="Move">&#9967;</td>

                    <td class="input-no">
                        {{index+1}}
                    </td>

                    <td><textarea class="input-name"  v-model="item.desc" @change="resizeTextarea($event)" ></textarea></td>

                    <td><input class="input-quantity" type="number" v-model.lazy="item.qty" @change="calculate(index)"></td>

                    <td><input class="input-unit" type="text" v-model="item.unit" ></td>

                    <td>￥<input class="input-price" type="number" v-model.lazy="item.price" @change="calculate(index)"></td>

                    <td class="input-amount">￥{{item.amount}}</td>

                    <td><textarea class="input-note"  v-model="item.comment" @change="resizeTextarea($event)"></textarea></td>

                    <td class='delete-button' v-on:click="confirmBox=true">

                        <span title="Delete Item" > &#9932; </span>

                    </td>
                </template>
                <template v-if="item.desc == 'Consumption Tax'">
                    <td class="vanish-cell" ></td>

                    <td class="input-no">
                        {{index+1}}
                    </td>

                    <td style="text-align:left; padding:6px;">Consumption Tax</td>

                    <td>{{item.qty}}</td>

                    <td><input class="input-unit" type="text" v-model.lazy="item.unit" @change="calculate(index)"></td>

                    <td>￥{{item.price}}</td>

                    <td class="input-amount">￥{{item.amount}}</td>

                    <!-- <td><input class="input-note" type="text" v-model="item.comment"></td>

                    <td class='vanish-cell'></td> -->
                </template>
            </tr>
            </tbody>
            <tr class="final-row">
                <td class="vanish-cell"></td>
                <td title="Add New Item" class="add-new-item"><div v-on:click="addNewItem">+</div></td>
                <td colspan="4" ><b>Initial Cost</b></td>
                <td>￥{{initialCost}}</td>
            </tr>

        </table>

    </div>

    <confirmation-modal v-if="confirmBox" v-on:returnAnswer="keepOrDelete($event)"></confirmation-modal>
  </div>
</template>

<script>

import ConfirmationModal from './ConfirmationModal.vue'

export default {
  name: 'intallation-items',
  props: ['initialItems'],
  components:{
      'confirmation-modal': ConfirmationModal
  },
  data () {
    return {
      currentIndex:undefined,
      initialCost: 0,
      confirmBox:false,
    }
  },
  methods:{
      rowClicked(index,event){
          this.currentIndex = index;
        //   console.log(this.initialItems);
      },

      calculate: function(index){
          this.initialItems[index].amount = this.initialItems[index].qty * this.initialItems[index].price;
          this.findInitialCost();
      },
      findInitialCost: function(){
          let sum = 0;
          let taxPercentage = 0;
          this.initialItems.forEach(function(item){
              if(item.desc == 'Consumption Tax'){
                  taxPercentage = parseFloat(item.unit);
                  console.log(taxPercentage);
              }else{
                  sum+=item.amount;
              }

          });
          let tax= parseInt(sum*taxPercentage/100);
          console.log(tax);
          this.initialItems[this.initialItems.length-1].price = tax;
          this.initialItems[this.initialItems.length-1].amount = tax;
          this.initialCost = sum+tax;
          this.$emit('initialCostChanged',this.initialCost);
        //   console.log(this.initialCost);
      },
      addNewItem(){
          this.initialItems.splice(this.initialItems.length-1,0,{
                "item":this.initialItems.length,
                "desc":"",
                "qty":1,
                "unit":"",
                "price":0,
                "amount":0,
                "comment":""
          });
          this.initialItems[this.initialItems.length-1].item = this.initialItems.length;
      },
      keepOrDelete(answer){
          this.confirmBox = false;
        //   console.log(answer)
          if(answer===true){
              this.initialItems.splice(this.currentIndex,1);
          }
          this.findInitialCost()
      },
      resizeTextarea(event){
        //   console.log(event.srcElement.scrollHeight);

        // We need to first set the height to 0 beacuse scrollHeight will be equal to height(when it is specified)
          event.target.style.height = "0px";
          let height = event.srcElement.scrollHeight;
          event.target.style.height = String(height) + "px";
      }

  },
  created(){
      this.findInitialCost();
  },
  mounted () {
      //   update textarea height after dragging
    this.$dragging.$on('dragend', () => {
        let allTextarea = document.querySelectorAll("#installation-items table textarea");
        allTextarea.forEach(element => {
        // We need to first set the height to 0 beacuse scrollHeight will be equal to height(when it is specified)
        element.style.height = '0px';
        let scroll_height = element.scrollHeight;
        // console.log(scroll_height);
        element.style.height = String(scroll_height) + 'px';
        });
    })
  }
}
</script>

<style lang="scss">
#installation-items{
    margin-top: 100px;

}
table{
    width: 80%;
    margin: auto;
    border-collapse: collapse;
    tr:nth-child(odd){
        background: #e6eaef;
    }
    tr{
        th{
            padding: 10px;
            background: #c6cdd6;
            border: 1px solid #2c3e50;
        }
        td{
            padding: 0px;
            text-align: center;
            border: 1px solid #2c3e50;
            align-items: center;
        }

        td.move-btn{
            border:2px solid white;

            border-top-left-radius: 50%;
            border-bottom-left-radius: 50%;
            background: #2c3e50;
            color: white;
            font-size: 30px;
            text-align: center;
            padding: 10px;
            cursor: move;
            &:hover{
                color: rgb(109, 181, 240);
                transition: 0.1s all ease-in;
            }
        }

        th.vanish-cell,td.vanish-cell{
            background:white;
            border: none;
        }

        td input{
            box-sizing: border-box;
            border: none;
            font-size: 16px;
            font-family: 'Lato', sans-serif;
            outline: none;
            width:100%;
            height: 100%;
            padding: 10px;
            color: #2c3e50;
            background: transparent;
        }

        td.input-no{
            border: 1px solid #2c3e50;
        }
        .input-quantity,.input-unit{
            width: 80px;
            text-align: center;
        }
        .input-price{
            width: 100px;
            text-align: right;
            padding-left: 0;
        }
        td.input-amount{
            text-align: right;
            padding: 6px;
        }
        td.delete-button{
            border:2px solid white;

            border-top-right-radius: 10px;
            border-bottom-right-radius: 10px;
            background: #2c3e50;
            color: rgb(238, 20, 20);
            font-size: 20px;
            text-align: center;
            padding: 10px;
            cursor: pointer;

            &:hover{
                color: rgb(248, 148, 148);
                transition: 0.1s all ease-in;
            }
        }

    }
    tr:hover{
        background: #a3bee0;
        td{
            input{
                background: #a3bee0;
            }
        }

    }

    tr.final-row{
        background:#c6cdd6;

        &:hover{
            background:#c6cdd6;
        }
        td:nth-child(3){
            text-align: center;
            font-size: 20px;
            font-weight:bold;
            border: none;
        }
        td:nth-child(4){
            text-align: right;
            padding: 10px;
            font-weight:bold;
            border:none;
            border-left: 1px solid #2c3e50;
        }
    }
    td.add-new-item{
        border:none;
        background: white;
        div{
            border-bottom-left-radius: 10px;
            font-size: 40px;
            font-weight: 900;
            background: #2c3e50;
            color: white;
            padding: 10px 10px;
            cursor: cell;
            &:hover{
                color: rgb(23, 158, 11);
                transition: 0.1s all ease-in;
            }
        }
    }
textarea{
    resize: none;
	width: 100%;
	padding: 6px;
	overflow: hidden;
	box-sizing: border-box;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    color: #2c3e50;
    background: transparent;
    border:none;
    outline: none;
    vertical-align: bottom;

}


}


</style>



